.buynow-btn {
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  padding-left: 43px;
  padding-right: 49px;
  margin-bottom: 19px;
  align-items: center;
  text-align: center;
  height: 44px;

  border: none;
  background-color: #fff;
  color: $accented-text;

  @media screen and(min-width: $desctop) {
    position: absolute;
    left: 870px;
    width: 159px;
    height: 44px;
    // left: 956px;
    // top: 38px;
  }

  .menu-close__icon-arrow {
    stroke: $accented-text;
  }
  @media screen and(max-width: $to-tablet) {
    width: 209px;
  }
  @media screen and(min-width: $tablet) {
    width: 186px;
  }
  @media screen and(min-width: $desctop) {
    margin-left: 110px;
  }

  font-weight: 700;
  font-size: 16px;
  line-height: 1.31;

  border-radius: 22px;

  transition-property: background-color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    background-color: $light-btn-hover-bgc;
  }
}
.buynow-btn__tablet {
  display: none;

  // display: none;
  // @media screen and(min-width: $tablet) {
  //   display: block;
  //   position: absolute;
  //   z-index: 15;
  //   font-style: normal;
  //   font-weight: bold;
  //   font-size: 16px;
  //   line-height: 21px;
  //   padding-left: 14px;
  //   align-items: center;
  //   text-align: center;
  //   background-color: #fff;
  //   color: $accented-text;
  //   border-radius: 22px;
  //   border: none;
  //   background-color: #fff;
  //   position: absolute;
  //   width: 160px;
  //   height: 44px;
  //   left: 574px;
  //   top: 38px;
  &:hover,
  &:focus {
    background-color: $light-btn-hover-bgc;
  }
  @media screen and(min-width: $tablet) and(max-width: $to-desctop) {
    display: block;
    top: 8px;
    left: 483px;
    z-index: 10;
    z-index: 100;
  }
}

.menu-close__icon-arrow-tablet {
  stroke: $accented-text;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.31;

  border-radius: 22px;

  transition-property: background-color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
